#notifications {
    display: block;
    position: absolute;
    top: 60px;
    right: 100px;
    text-align: center;
    margin: 1em;
    z-index: 4;
}

.notification>span {
    display: inline-block;
    padding:  0.5em 2em 0.5em 2em;
    border-radius: 10px;
}

#loadingBox>span {
    background: #7CB3E9;
}
#infoBox>span {
    background: #393;
}
#errorBox>span {
    background: #F50;
}